.full {
  z-index: 1000;
  overflow: hidden;
  position: fixed;
  top: 0px;
  width: 100%;
}
li {
  list-style: none;
}
.menu {
  margin: auto;
  max-width: 1040px;
  overflow: hidden;
  padding: 0px 20px 0px 20px;
}

.menu-item {
  color: #b9b8b8;
  text-decoration: none;
  transition: 0.8s;
  transition-property: color;
  text-align: center;
}

.menu-item:hover {
  color: rgb(219, 209, 209);
  text-decoration: none;
}
.menu-item:active {
  color: #79797c;
  text-decoration: none;
}
.apple-logo {
  color: rgb(214, 214, 214);
  /* font-size: 21px; */
  transition: 0.8s;
  transition-property: color;
}
.apple-position {
  line-height: 44px;
  position: relative;
  bottom: 2px;
}
.apple-logo:hover {
  color: rgb(235, 230, 230);
}
.no-dec-btn,
.no-dec-btn:focus {
  background: none;
  outline: none;
  border: none;
  padding: none;
  margin: none;
  overflow: visible;
}

.btn-one:focus,
.btn-two:focus {
  outline: none;
}
.one {
  transform: rotate(45deg) translate(6px, 0px);
  -webkit-animation: one-anim 0.5s 1;
          animation: one-anim 0.5s 1;
}
.two {
  transform: rotate(-45deg) translate(-5px, -2px);
  -webkit-animation: two-anim 0.5s 1;
          animation: two-anim 0.5s 1;
}
@-webkit-keyframes one-anim {
  0% {
    transform: rotate(0deg) translate(0px, 0px);
  }
  5% {
    transform: rotate(0deg) translate(3px, 0px);
  }
  100% {
    transform: rotate(45deg) translate(6px, 0px);
  }
}
@keyframes one-anim {
  0% {
    transform: rotate(0deg) translate(0px, 0px);
  }
  5% {
    transform: rotate(0deg) translate(3px, 0px);
  }
  100% {
    transform: rotate(45deg) translate(6px, 0px);
  }
}
@-webkit-keyframes two-anim {
  0% {
    transform: rotate(0deg) translate(0px, 0px);
  }
  5% {
    transform: rotate(0deg) translate(-3px, 0px);
  }
  100% {
    transform: rotate(-45deg) translate(-5px, -2px);
  }
}
@keyframes two-anim {
  0% {
    transform: rotate(0deg) translate(0px, 0px);
  }
  5% {
    transform: rotate(0deg) translate(-3px, 0px);
  }
  100% {
    transform: rotate(-45deg) translate(-5px, -2px);
  }
}
.no-one {
  transform: rotate(0deg) translate(0px, 0px);
  -webkit-animation: no-one-anim 0.5s 1;
          animation: no-one-anim 0.5s 1;
}
.no-two {
  transform: rotate(0deg) translate(0px, 0px);
  -webkit-animation: no-two-anim 0.5s 1;
          animation: no-two-anim 0.5s 1;
}
.no-onee {
  transform: rotate(0deg) translate(0px, 0px);
}
.no-twoo {
  transform: rotate(0deg) translate(0px, 0px);
}
@-webkit-keyframes no-one-anim {
  0% {
    transform: rotate(45deg) translate(6px, 0px);
  }
  30% {
    transform: rotate(0deg) translate(3px, 0px);
  }
  100% {
    transform: rotate(0deg) translate(0px, 0px);
  }
}
@keyframes no-one-anim {
  0% {
    transform: rotate(45deg) translate(6px, 0px);
  }
  30% {
    transform: rotate(0deg) translate(3px, 0px);
  }
  100% {
    transform: rotate(0deg) translate(0px, 0px);
  }
}
@-webkit-keyframes no-two-anim {
  0% {
    transform: rotate(-45deg) translate(-5px, -2px);
  }
  30% {
    transform: rotate(0deg) translate(-3px, 0px);
  }
  100% {
    transform: rotate(0deg) translate(0px, 0px);
  }
}
@keyframes no-two-anim {
  0% {
    transform: rotate(-45deg) translate(-5px, -2px);
  }
  30% {
    transform: rotate(0deg) translate(-3px, 0px);
  }
  100% {
    transform: rotate(0deg) translate(0px, 0px);
  }
}
